.thank-you-wrapper{height:100vh;background:#fff}.thank-you{display:flex;justify-content:center;align-items:center;height:100vh;background:silver;font-family:"Mulish",sans-serif}.thank-you .congrat{background-color:#fff;padding:50px;border-radius:12px;box-shadow:0 10px 20px rgba(0,0,0,.1);text-align:center;max-width:450px;width:100%;color:#333}.thank-you .congrat h1{font-size:2.8rem;color:#378b39;margin-bottom:15px;text-transform:capitalize}.thank-you .congrat h1::before{content:"🎉 "}.thank-you .congrat h1::after{content:" 🎉"}.thank-you .congrat p{font-size:1.2rem;margin-bottom:20px;line-height:1.6}.thank-you .congrat p:first-of-type{margin-bottom:10px;font-weight:bold}.thank-you .congrat p:last-of-type{font-size:1rem;color:#888}.thank-you .congrat a{display:inline-block;margin-top:30px;padding:12px 25px;background-color:#ff1e00;color:#fff;text-decoration:none;font-size:1.1rem;font-weight:500;border-radius:25px;transition:background-color .3s ease,transform .3s ease}.thank-you .congrat a:hover{background-color:#a40707;transform:translateY(-3px)}