.dashboard-wrapper{
  height:100vh;
  background: white;
}
.dashboard{
    h3{
        color:  rgb(82, 29, 150);
    }


    .logout{
        display: flex;
        flex-direction: column;
        .wrap-logout{
            background-color: rgb(82, 29, 150);
            display: flex;
            place-content: space-between;
            font-size: 18px;
            padding: 6px 20px;
            align-items: center;
            span{
                font-weight: 500;
                color: rgba(245, 245, 245, 0.886);
            }
            button{
                border: none;
                padding: 10px 30px;
                font-size: 18px;
                cursor: pointer;
                display: flex;
                align-items: center;
                gap: 7px;
                background-color: rgb(242, 165, 147);
                color: rgb(0, 0, 0);
                // font-weight: 600;
                border-radius: 5px;
                // box-shadow: 2px 2px 6px rgb(10, 10, 10);
                font-family: "Mulish", sans-serif;
                &:hover{
                    background-color: rgba(240, 166, 152, 0.803);
                }
            }
        }
        .goodluck{
            align-self: center;
            font-size: 2rem;
            text-transform: capitalize;
            padding: 12px;
            color: rgb(0, 41, 128);
        }
    }

    section {
        .sub-parent-wrapper {
          display: flex;
          justify-content: space-between;
          padding: 20px 40px;
        //   align-items: flex-start;
        //   gap: 20px; // Add gap between the columns
      background-color: whitesmoke;
   
    //   align-items: center;
          .left, .mid, .right {
            flex: 1;
            // Adding a small margin to avoid content being too close to each other
            margin: 0 10px;
            padding: 30px 25px;
            border-radius: 10px;
            box-shadow: 2px 2px 6px rgb(203, 203, 203);
          }
      
          .left {
            display: flex;
            flex-direction: column;
            text-transform: capitalize;
            // font-size: 1.2rem;
            gap: 1rem; // Adjust gap between elements
            // background-color:  rgba(151, 200, 201, 0.805);
            // color: whitesmoke;
            .subject {
                display: flex;
                flex-direction: column;
                gap: 6px;
              
            }
            .subjects {
                display: flex;
                gap: 12px;
                font-weight: 500;
                // label{
                //     font-weight: 500;
                //     color: rgb(60, 65, 44);
                // }
                label,span{
                    text-transform: capitalize;
                    display: block;
                }
                span{
                    color: rgb(92, 92, 92);
                }
            }
          }
      
          .mid {
            display: flex;
            flex-direction: column;
            align-items: center; // Center align the content
            text-align: center; // Center align text
            place-content: space-between;
            // background-color: rgba(189, 146, 195, 0.847);
            .mid-top{
                display: flex;
                flex-direction: column;
                place-content: center;
                align-items: center;
                gap: 12px;
                p{
                    text-transform: capitalize;
                    font-weight: 500;
                    font-size: 20px;
                    display: flex;
                    place-content: space-between;
                    gap: 12px;
                    span{
                        color: rgb(92, 92, 92);
                    }
                }
            }
            .img {
              position: relative;
              width: 200px;
              height: 200px;
              border: 2px solid rgba(128, 128, 128, 0.09);
              border-radius: 50%;
              overflow: hidden; // Ensure image doesn't overflow its container
              svg{
                width: 100%;
                height: 100%;
              }
              img {
                width: 100%;
                height: 100%;
                object-fit: cover; // Ensure image covers the entire container
                object-position: center;
                overflow: hidden;
              }
            }
      
            form {
              margin-top: 3.3rem; // Add some space between the image and the button
              button {
                background-color: green;
                color: #fff;
                border: none;
                padding: 12px 30px;
                font-size: 1rem;
                border-radius: 5px;
                cursor: pointer;
                transition: background-color 0.3s ease;
                text-transform: capitalize;
      
                &:hover {
                  background-color: rgba(0, 128, 0, 0.725);
                }
              }
            }
          }
      
          .right {
            display: flex;
            flex-direction: column;
            gap: 1rem;
            // background-color: rgba(255, 165, 30, 0.766);
            h3 {
                text-transform: capitalize;
              margin-bottom: 1rem;
            }
      
            .instr {
              display: flex;
              flex-direction: column;
              gap: 10px;
      
              p {
                margin: 0;
                font-size: 1rem;
                color: rgb(92, 92, 92);
                span{
                    color: black;
                }
                span {
                  font-weight: bold;
                  margin-right: 10px;
                }
              }
            }
          }
        }
      }

      footer{
        background-color: rgba(213, 213, 213, 0.649);
        // color: white;
        text-align: center;
        font-size: 19px;
        padding: 2rem 0;
        font-family: 'Mulish', sans-serif;
      }
      
}