.show-btn {
  display: grid;
  grid-template-columns: auto auto auto auto auto;
  background-color: rgb(157, 151, 151);
}

@media (min-width: 800px) {
  .show-btn {
    border-bottom-left-radius: 10px;
    border-bottom-right-radius: 10px;
  }
}

.equal {
  grid-column-start: 4;
  grid-column-end: 6;
}

.btn:last-child {
  font-weight: medium;
  font-size: 40px;
  width: 110px;
  height: 50px;
  border-radius: 10px;
  color: white;
  background-color: #2f236c;
  /* padding-bottom: 6px; */
}

.clr {
  color: #6dd6ae;
}

.exp {
  color: #06beb6;
}

.btn {
  width: 60px;
  height: 60px;
  margin: 6px auto;
  font-size: 24px;
  font-weight: 500;
  cursor: pointer;
  border-radius: 50%;
  background: none;
  border: 0px;
  /* user-select: none; */
  /* background-color: yellow; */
}

@media (min-width: 800px) {
  .btn:hover {
    background-color: #3013a4;
    color: white;
    transition: background-color 0.3s ease;
    /* border-radius: 50%; */
  }

  .btn:active,
  .btn.touched {
    background-color: #2e1088;
    color: white;
    transition: background-color 0.3s ease;
    border-radius: 50%;
  }

  .btn:active {
    transform: scale(0.9);
  }

  .btn.touched {
    pointer-events: none;
  }
}

@media (max-width: 800px) {
  .btn:active {
    transform: scale(0.9);
  }
}
