
$blue : #008080;
$grey : rgb(250, 250, 250);
$green : rgb(14, 183, 14);
$navy : navy;
$yellow : yellow;
$green2 : rgba(201, 219, 153, 0.799);

.can-check-result{
    margin-top: 1rem;
    a{
        font-size: 1.3rem;
        border: 2px solid grey;
        text-decoration: none;
        padding: 6px 12px;
        color: rgb(255, 202, 183);
        border-radius: 4px;
        background-color: rgb(106, 106, 106);
        &:hover{
            color: rgb(253, 217, 120);
        }
    }
    // background: green;
}


.Home-wrapper{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

}
.Home{
    position: relative;
    width: 80%;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 20px 30px;
    gap: 4cm;
    border-radius: 5px;
    .main-heading{
        color: whitesmoke;
        font-size: 3rem;
        // border: 2px solid red;
        width: 100%;
        text-align: center;
    }
    marquee{
        // color: whitesmoke;
        position: absolute;
        top: 7em;
        width: 80%;
        border-bottom: 2px solid rgb(201, 202, 202);
        background-color: white;
        padding: 6px;
        border-radius: 5px;
        border-left: 4px solid rgb(255, 153, 0);
        border-right: 4px solid rgb(255, 153, 0);
    }
    .ExamCord{
        // border: 1px solid red;
        align-self: normal;
        display: flex;
        justify-content: space-between;
        flex-wrap: wrap;
        gap: 20px;
        .Cordinator,.TakeExam{
            border-radius: 5px;
            // flex-basis: 38%;
            width: 430px;
            height: 230px;
            text-align: center;
            text-decoration: none;
            padding: 40px 0;
            font-size: 20px;
            &:hover{
                background: $green2;
            }
        }
        .TakeExam{
            background: $blue;
            color: white;
            // box-shadow: 2px 2px 7px grey;
            border: 1px solid rgb(216, 215, 215);
            // padding:2rem;
        }
        .Cordinator{
            // box-shadow: 2px 2px 7px rgba(0, 0, 0, 0.644);
            border: 1px solid rgb(216, 215, 215);
            background: rgb(0, 70, 128);
            color: white;

        }
    }
}