$blue : rgb(130, 130, 203);
.ExamPortal{
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 2em;
    form{
        margin-top: 1.5cm;
        display: flex;
        flex-direction: column;
        // border: 1px solid red;
        // box-shadow: 2px 3px 7px black;
        width: 31rem;
        gap: 1.3em;
        border-radius: 5px;
        padding: 3em 2em;
        background: #00000014;
        button,input{
            font-size: 25px;
            padding: 12px 9px;
        }
        input{
            border-radius: 5px;
            border: 1px solid transparent;
            &:focus{
                outline: none;
            }
        }
        button{
            background: rgb(6, 117, 6);
            border: none;
            font-weight: 500;
            color: white;
            border-radius: 5px;
            cursor: pointer;
            // margin-top: 2em;
            &:hover{
                background: rgb(76, 173, 76);
                // color: #000;
            }
        }
    }

    a{
        padding: 6px 30px;
        // background: linear-gradient(to right,rgb(51, 7, 63)90%, white);
        text-decoration: none;
        color: black;
        font-weight: 550;
        display: flex;
        align-items: center;
        gap: .4em;
        border-radius: 5px;
        transition: ease-in-out 3s;
        border: 1px solid grey;
        &:hover{
            transition: ease-in-out 3s;
            // background: linear-gradient(to left,rgb(51, 7, 63)90%, white);
        }
    }
}

#error{
    color :white;
    background: rgb(154, 7, 7);
    border: 1px solid red;
    padding: 6px 5px;
    border-radius: 3px;
    margin-top: -1em;
    
}
